  transform: rotate(-45deg);
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -1.5px;
  margin-left: -10px;
  display: block;
  height: 3px;
  width: 20px;
  transition: all 0.25s ease-out;
}
.close:after {
  transform: rotate(-135deg);
}
